import React, { useState, useEffect } from "react";
import s from "./Cooperation.module.css";
import nastya from "./images/Nastya_1.png";
import mail from "./images/mail.svg";
import telephone from "./images/telephone.svg";
import Button from "../Button/Button";

const Cooperation = ({setModalActive}) => {

    const [email, setEmail] = useState('');
    const [text, setText] = useState('');
    const [emailDirty, setEmailDirty] = useState(false);
    const [textDirty, setTextDirty] = useState(false);
    const [emailError, setEmailError] = useState("    ");
    const [textError, setTextError] = useState("    ");
    const [formValid, setFormValid] = useState(false);

    useEffect (() => {
        if (emailError || textError) {
            setFormValid(false)
        } else {
            setFormValid(true)
        }
    }, [emailError, textError])

    const sendRequest = (e) => {
        e.preventDefault();
        console.log(email, text);
        setEmail("");
        setText("");

        if(!formValid) {
            return;
        }

        setModalActive(false);
    }

    const emailHandler = (e) => {
        setEmail(e.target.value)
        const re =
  /^(([^<>()[\]\.,;:\s@\"]+(\.[^<>()[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i;
        if(!re.test(String(e.target.value).toLowerCase())) {
            setEmailError(" email");
        } else {
            setEmailError("");
        } 
    }

    const textHandler = (e) => {
        setText(e.target.value)
        if (e.target.value.length < 3) {
            setTextError("    3 ")
            if(!e.target.value) {
                setTextError("    ")
            }
        } else {
            setTextError("")
        }
    }

    const blurHandler = (e) => {
        // eslint-disable-next-line default-case
        switch (e.target.name) {
            case 'email' :
                setEmailDirty(true)
                break
            case 'text' :
                setTextDirty(true)
                break
        }
    }

    return (
        <div className={s.contact}>
            <div className={s.contactLeft}>
                <h3 className={s.modalTitle}>    </h3>
                <div className={s.modalSubtitle}>    </div>
                <ul className={s.info}>
                    <li className={s.infoItem}>
                        <img className={s.infoIcon} src={telephone} alt="telephone" />
                        +7 983 123 33 84
                    </li>
                    <li className={s.infoItem}>
                        <img className={s.infoIcon} src={mail} alt="mail" />
                        nastya.v.m@mail.ru
                    </li>
                </ul>

                <form className={s.form} action="/" method="post">
                    <div className={s.formGroup}>
                        <label className={s.label} htmlFor="input-email">   </label>
                        {(emailDirty && emailError) && <div style={{color:"red"}}>{emailError}</div>}
                        <input className={s.input} 
                        type="text" 
                        id="input-email" 
                        placeholder="  "
                        value={email}
                        onChange={e => emailHandler(e)}
                        name ="email"
                        onBlur={e => blurHandler(e)}
                        />
                    </div>
                    <div className={s.formGroup}>
                        <label className={s.label} htmlFor="input-text"> </label>
                        {(textDirty && textError) && <div style={{color:"red"}}>{textError}</div>}
                       <textarea 
                       name="text" 
                       id="input-text" 
                       className={s.textarea} 
                       placeholder=" ..."
                       value={text}
                       onChange={e => textHandler(e)}
                       onBlur={e => blurHandler(e)}
                       />
                    </div>
                    <div className={s.btnRight}>
                        <Button
                            disabled={!formValid}
                            type="submit" 
                            onClick={sendRequest}
                            ></Button>
                    </div>
                </form>
            </div>
            <div className={s.contactRight}>
                <img className={s.img} src={nastya} alt="Nastya" />
            </div>

        </div>
    )
}

export default Cooperation;